import { useEffect, useState } from "react"
import { useNavigate, useSearchParams } from "react-router-dom"
import { CLERK_TICKET } from "@rp/common/settings"
import { useFromNative } from "@rp/react/web/common/hooks"
import { INVITATION_ROUTE } from "@rp/react/web/router"
import { AccountDetailsStep } from "./Steps/AccountDetailsStep"
import { EmailVerificationStep } from "./Steps/EmailVerificationStep"
import { PasswordStep } from "./Steps/PasswordStep"

const SignUpPage = () => {
  const [searchParams] = useSearchParams()
  const navigate = useNavigate()

  const steps = [AccountDetailsStep, PasswordStep, EmailVerificationStep]
  const [step, setStep] = useState(0)

  const { setFromNative } = useFromNative()

  useEffect(() => {
    setFromNative()
  }, [setFromNative])

  useEffect(() => {
    const clerkTicket = searchParams.get(CLERK_TICKET)
    if (clerkTicket) {
      const params = new URLSearchParams(searchParams)
      navigate(`${INVITATION_ROUTE}?${params.toString()}`)
    }
  }, [searchParams, navigate])

  const CurrentStep = steps[step]

  if (!CurrentStep) {
    return null
  }

  return <CurrentStep onComplete={() => setStep(v => v + 1)} />
}

export { SignUpPage }
